import React, { useState } from "react";
import { motion } from "framer-motion";

import { Swiper, SwiperSlide } from "swiper/react";

import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";

// import required modules
import { Pagination, Navigation } from "swiper";

function ImageModal({ showMM, item, setShowWork }) {
  const [showModal, setShowModal] = useState(showMM);

  console.log(item);
  return (
    <>
      {showModal && item.images.legth > 0 && (
        <motion.div
          className="fixed w-screen h-screen flex items-center top-0 left-0 right-00 bottom-0 justify-center z-50"
          initial={{ opacity: 0, scale: 0 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ delay: 0, duration: 1, type: "spring", stiffness: 100 }}
        >
          <div className=" relative z-30 bg-white rounded-lg h-fit w-4/5  p-6">
            <motion.div
              className="content"
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 1, duration: 0.3 }}
            >
              <div className="header text-2xl font-semibold">
                <div className=" text-center"> {item.name} </div>
                <div>
                  <Swiper
                    slidesPerView={2.3}
                    spaceBetween={0}
                    pagination={{
                      clickable: true,
                    }}
                    navigation={true}
                    modules={[Pagination, Navigation]}
                    className="mySwiper"
                  >
                    {item.images.map((item, index) => (
                      <SwiperSlide key={index}>
                        <div className="py-10 px-2 pr-6  flex flex-col items-center justify-center ">
                          {/* <p> {item}</p> */}
                          <img
                            src={item.image}
                            width="600"
                            height="300"
                            className=" rounded-xl mx-10"
                          />
                          <div className="text-center text-base font-normal mt-3">
                            {item.name}
                          </div>
                        </div>
                      </SwiperSlide>
                    ))}
                  </Swiper>
                </div>
              </div>
            </motion.div>
          </div>
          <div
            className="absolute z-20 top-0 bottom-0 left-0 right-0 bg-gray-700 bg-opacity-90 "
            onClick={() => setShowWork(false)}
          ></div>
        </motion.div>
      )}
    </>
  );
}

export default ImageModal;
